body {
	background: #FFF;
	line-height: 1.4; }

.nopadding {
	padding: 0 !important; }

.container-inner {
	margin: auto;
	padding-left: 15px;
	padding-right: 15px;
	width: 100%;
	max-width: 1170px;
	@include r(1199) {
		max-width: 970px; }
	@include r(991) {
		max-width: 750px; }
	@include r(767) {
		padding: 0; } }


