.news {
	min-width: 320px;
	padding-top: 98px;
	padding-bottom: 60px;
	@include r(767) {
		padding-top: 35px;
		padding-bottom: 25px; }

	&__container {}

	&__title {
		margin-bottom: 37px;
		@include r(767) {
			margin-bottom: 14px; } }

	&__item {}

	&__col-link {
		display: flex;
		justify-content: flex-end; }

	&__link {
		margin-top: 48px;
		@include r(1199) {
			margin-top: 33px; } } }

