.clients {
	min-width: 320px;
	position: relative;
	background-color: $light-gray;
	@include r(767) {
		margin-top: 0;
		background-color: $white; }

	&__container {}

	&__background {
		position: absolute;
		width: 100%;
		height: 900px;
		background: url("../img/clients-background.png") no-repeat center;
		background-size: cover;
		@include r(1199) {
			height: 752px; }
		@include r(991) {
			height: 600px; }
		@include r(767) {
			display: none; } }

	&__wrapper {
		width: 100%;
		max-width: 1322px;
		margin: 389px auto 0;
		background-color: $white;
		@include r(1199) {
			margin-top: 315px;
			max-width: 1100px; }
		@include r(991) {
			margin-top: 239px;
			max-width: 860px; }
		@include r(767) {
			margin-top: 0px; } }


	&__title {
		margin: 88px 0 70px;
		@include r(767) {
			margin: 8px 0 0;
			text-align: center; } }

	&__item {}

	&__col-link {
		display: flex;
		justify-content: flex-end; }

	&__link {
		margin-top: 72px; } }
