.button {
	@include markpro;
	font-weight: 400;
	color: $white;
	text-decoration: none;
	text-transform: uppercase;
	font-size: 12px;
	letter-spacing: 2px;

	&_pink {
		display: inline-block;
		background-color: $pink;
		padding: 10px 30px;
		&:after {
 			background-color: $white; } }

	&_pink_lg {
		padding: 20px 23px; }

	&_blue {
		color: $blue;
		&:after {
			background-color: $blue; } }

	&_decorated {
		display: flex;
		align-items: center;
		position: relative;
		&:after {
			margin-left: 21px;
			content: '';
			display: inline-block;
			width: 30px;
			height: 1px; } }

	&_decorated_sm {
		&:after {
			margin-left: 6px; } } }
