.header {
	transition: 600ms;
	position: absolute;
	width: 100%;
	min-width: 320px;
	z-index: 1;

	&__container {
		padding-top: 52px;
		@include r(767) {
			padding-top: 24px; } }

	&__logo {
		.icon {
			margin-left: -4px;
			font-size: 32px;
			fill: $white; } }

	&__nav {}

	&__toggle {
		width: 33px;
		height: 24px;
		cursor: pointer; }

	&__toggle-container {
		display: none;
		justify-content: flex-end;
		@include r(991) {
			display: flex; } }

	&__toggle-lines {
		transition: 600ms;
		position: relative;
		display: flex;
		height: 2px;
		width: 33px;
		background-color: $white;
		margin-top: 11px;
		&:before, &:after {
			transition: 600ms;
			content: '';
			position: absolute;
			width: 33px;
			height: 2px;
			background-color: $white; }
		&:before {
			top: 11px; }
		&:after {
			bottom: 11px; } }
	//Opened menu state
	&_opened {
		position: fixed;
		background-color: $purple;
		width: 100%;
		height: 100%;

		.header__toggle-lines {
			background-color: transparent;
			&:after {
				top: 0;
				transform: rotate(45deg); }
			&:before {
				top: 0;
				transform: rotate(-45deg); } } } }


