.mediators {
	background-color: #f1f1f1;
	padding-top: 150px;
	padding-bottom: 120px;
	margin-bottom: 75px;
	min-width: 320px;
	@include r(767) {
		padding-top: 50px;
		padding-bottom: 50px;
		margin-bottom: 25px; }


	&__container {}

	&__title {
		margin-bottom: 10px;
		@include r(991) {
			margin-bottom: 63px; }
		@include r(767) {
			margin-bottom: 14px; } }

	&__list {}

	&__item {
		margin-top: 83px; }

	&__col-link {
		display: flex;
		justify-content: flex-end; }

	&__link {
		margin-top: 70px;
		@include r(991) {
			margin-top: 52px; } }

	&__card-list {} }



