.advantage {
	position: relative;

	&__image {
		width: 100%;
		height: 672px;
		object-fit: cover;
		@include r(1199) {
			height: 586px; }
		@include r(991) {
			height: 450px; }
		@include r(767) {
			height: 185px; } }

	&__text {
		position: absolute;
		@include markpro;
		padding: 0 25px;
		font-weight: 700;
		font-size: 30px;
		line-height: 1.45;
		left: 50%;
		top: 50%;
		transform: translateX(-50%) translateY(-50%);
		width: 100%;
		text-align: center;
		color: $white;
		@include r(991) {
			font-size: 20px;
			padding: 0 12px; }
		@include r(767) {
			font-size: 16px; } } }
