//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }

// MarkPro
@font-face {
	@include fontdefault(normal, 400, 'MarkPro');
	@include fontpath('MarkPro Regular', 'MarkPro-Regular', MarkPro-Regular); }
@font-face {
	@include fontdefault(normal, 700, 'MarkPro');
	@include fontpath('MarkPro Bold', 'MarkPro-Bold', MarkPro-Bold); }

//FinancierDisplay
@font-face {
	@include fontdefault(normal, 400, 'FinancierDisplay');
	@include fontpath('FinancierDisplay Regular', 'FinancierDisplay-Regular', FinancierDisplay-Regular); }
@font-face {
	@include fontdefault(normal, 600, 'FinancierDisplay');
	@include fontpath('FinancierDisplay Semibold', 'FinancierDisplay-Semibold', FinancierDisplay-Semibold); }


