.news-article {
	height: 364px;
	@include r(767) {
		height: 324px; }

	&__image {
		width: 100%;
		object-fit: cover;
		height: 200px;
		@include r(1199) {
			height: 162px; }
		@include r(767) {
			height: 185px; } }

	&__category {
		@include markpro;
		color: $blue;
		font-size: 12px;
		font-weight: 400;
		letter-spacing: 2px;
		text-transform: uppercase;
		margin-top: 16px;
		@include r(991) {
			margin-top: 10px; } }

	&__item-title {
		@include financier;
		font-size: 24px;
		font-weight: 400;
		letter-spacing: 0.15px;
		margin-top: 8px;
		line-height: 1.2;

		a {
			color: $gray; } }

	&__descr {
		@include markpro;
		line-height: 1.57;
		color: $gray;
		font-size: 14px;
		margin-top: 8px; } }
